/** @format */
* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  cursor: url(data:image/png;base64,), auto;
}
/* Hide scrollbar for Chrome, Safari and Opera */
/* width */
::-webkit-scrollbar {
  width: 0px;
  height: 0;
}

/* Track */
::-webkit-scrollbar-track {
  background: #14343f;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ffc500;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/** @format Nabvar*/

.header-navbar {
  top: 0;
  height: 64px;
  -webkit-align-items: center;
          align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  z-index: 100;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
  padding: auto;
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}

.header-navbar .borderClick {
  width: 0px;
  height: 0px;
}
.header-navbar .navbarDiv3 .borderClick {
  width: 0px;
  height: 0px;
  float: right;
  margin-right: 110px;
}
@media only screen and (min-width: 600px) {
  .header-navbar {
  }
  .header-navbar .navbarDiv2 .borderClick {
    margin-left: 30%;
    margin-right: 0;
  }

  .header-navbar .navbarDiv2 {
    width: 200px;
    position: absolute;
    right: 70px;
  }
  .header-navbar .navbarDiv15 {
    width: 160px;
    position: absolute;
    right: 280px;

    height: 64px;
    padding: auto;
  }
  .header-navbar .navbarDiv15 .resources-div {
    margin-top: 15px;
  }
  .header-navbar .navbarDiv3 {
    width: 60px;
    display: block;

    position: absolute;
    right: 0;
  }
  /*BOTON en modo desktop*/
  .header-navbar .navbarDiv2 .borderClick {
    float: right;
    margin-right: 150px;
  }
}

/** @format */

.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: 14px;
  margin-right: auto;
  background-color: transparent;
}

.button-hamburgerIcon .img-hamburger {
  height: 32px;
  vertical-align: middle;
}

.button-hamburgerIcon:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

/*icon pregunta*/
