/** @format */
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&display=swap");
:root {
  --azul: #14343f;
  --azulClaro: #14343f;
  --azulObscuro: #153540;
}
.div-movile {
  margin-top: 18px;
  background-color: var(--azulClaro);
  width: 100vw;
  margin-left: calc(+100vw - 300%);
  overflow: hidden;
  font-family: "Noto Sans JP", sans-serif;
  border-radius: 0px 0px 10px 10px;
}

.div-movile .li {
  text-decoration: none;
  display: inline-block;
  width: 100vw;
  margin-right: calc(-100vw + 100%);
  padding: 30px 32px 32px 20px;
  font-size: 20px;
  letter-spacing: 3.5px;

  text-align: justify;
}
.div-movile .li-start {
  width: 100%;
  padding: 30px 32px 32px 0px;
}
.div-movile .li-start .button {
  margin-left: 20px;
}
.div-movile .li-start:hover {
  background-color: #2e4a58;
  transition: background-color 100ms linear;
}

.div-movile .li:hover {
  cursor: pointer;
  text-decoration: underline #fff;
  background-color: #2e4a58;
  cursor: url("../../../../assets/cursors/hover.png"), auto;
  transition: background-color 100ms linear;
}
/*Icons*/
.div-movile .li .icons-menu {
  margin-bottom: -5px;
  margin-right: 5px;
  margin-left: 0px;
  color: #909090;
}
.div-movile .li .icons-menu-cloud {
  margin-bottom: -4px;
  margin-right: 7px;
  margin-left: 0px;
  color: #909090;
}
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap");
@media only screen and (min-width: 600px) {
  .div-movile {
    margin-top: 14px;
    background-color: #202020de;
    width: 300px;
    margin-left: -403%;
    overflow: hidden;
    font-family: "Roboto", sans-serif;
    border-radius: 0;
    border: solid 0px;
    border-color: #909090;
    border-top: 0;
  }
  .div-movile .li {
    font-size: 17px;
  }
  .div-movile .li:hover {
    cursor: pointer;
    text-decoration: underline #909090;
    background-color: #373737;
    cursor: url("../../../../assets/cursors/hover.png"), auto;
    transition: background-color 100ms linear;
  }
  .div-movile .li-start:hover {
    background-color: #373737;
    transition: background-color 100ms linear;
  }
  .div-movile .li-start .button {
    margin-left: 30px;
  }
}
