/** @format */
@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");
.div-retroButtont {
}
:root {
  --gris: #4ea6a6;
  --grisOscuro: #206f6f;
}
.Easy {
  outline: none;
  background-color: var(--gris);
  color: #fff;
  /*Size*/
  border-radius: 5px;
  font-size: large;
  font-family: "Press Start 2P";
  letter-spacing: 1.5px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: -3px 5px var(--grisOscuro);
  border: none;
  cursor: pointer;

  /*Margin*/
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
}

.Easy:active {
  color: #8eb9ca;
  color: var(--blue);
  background-color: var(--BlackBlue);
  box-shadow: 0 2px var(--blue);
  transform: translateY(4px);
  transition-duration: 0.03s;
}
.Easy:hover {
  cursor: url("../../../../assets/cursors/hover.png"), auto;
}
