/** @format Nabvar*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap");

.header-navbar {
  top: 0;
  height: 64px;
  align-items: center;
  background-color: #14343f;
  box-shadow: 1px 5px 4px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  position: sticky;
  width: 100%;
  z-index: 100;
}
/*Propiedades de los containers*/
.header-navbar .navbarDiv1 {
  display: block;
  float: left;
  padding: 0;
  margin: 0;
  height: 100%;
  width: 33.33%;
  outline: none;
}
.header-navbar .navbarDiv2 {
  width: 33.33%;
  float: left;
  overflow: hidden;
  height: 64px;
  padding: auto;
}

.header-navbar .navbarDiv3 {
  display: block;
  width: 33.33%;
  float: right;
  height: 100%;
}

.header-navbar .borderClick {
  width: 0px;
  height: 0px;
}
.header-navbar .navbarDiv3 .borderClick {
  width: 0px;
  height: 0px;
  float: right;
  margin-right: 110px;
}
@media only screen and (min-width: 600px) {
  .header-navbar {
  }
  .header-navbar .navbarDiv2 .borderClick {
    margin-left: 30%;
    margin-right: 0;
  }

  .header-navbar .navbarDiv2 {
    width: 200px;
    position: absolute;
    right: 70px;
  }
  .header-navbar .navbarDiv15 {
    width: 160px;
    position: absolute;
    right: 280px;

    height: 64px;
    padding: auto;
  }
  .header-navbar .navbarDiv15 .resources-div {
    margin-top: 15px;
  }
  .header-navbar .navbarDiv3 {
    width: 60px;
    display: block;

    position: absolute;
    right: 0;
  }
  /*BOTON en modo desktop*/
  .header-navbar .navbarDiv2 .borderClick {
    float: right;
    margin-right: 150px;
  }
}
