/** @format */

@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");
.div-ButtonStart {
  width: 120px;
  height: 45px;
}
.ButtonStart {
  outline: none;
  background-color: #ffc500;
  color: #fff;
  /*Size*/
  border-radius: 5px;
  font-size: large;
  font-family: "Press Start 2P";
  letter-spacing: 1.5px;
  text-shadow: 2px -1px #5c553e;
  padding: 10px;
  box-shadow: -3px 5px #c6571b;
  border: none;
  cursor: pointer;
  float: right;
  /*Margin*/
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
}

.ButtonStart:active {
  color: #ffc500;
  background-color: #b08d1c;
  box-shadow: 0 2px #ffc500;
  transform: translateY(4px);
  transition-duration: 0.03s;
}
.ButtonStart:hover {
  background-color: #fdd44d;
  cursor: url("../../assets/cursors/hover.png"), auto;
}
