/**
 * /*Button
 *
 * @format
 */
:root {
  --blue: #4ea6a6;
  --white: #ffffff;
  --BlackBlue: #206f6f;
}
.learn-code-input {
  cursor: pointer;
  display: block;
  outline: none;

  /*Size*/
  width: auto;
  height: auto;

  /*Colors */
  background-color: var(--blue);
  color: #fff;

  box-shadow: -3px 5px var(--BlackBlue);

  /*Margin*/
  margin: 0;
  margin-top: 5.5px;

  /*Padding*/
  padding: 10px 31px;

  /*TEXT*/
  font-size: 20px;
  font-family: "Press Start 2P";
  letter-spacing: 0px;

  /*Border*/
  border-radius: 5px;
  border: none;
  cursor: url("../../assets/cursors/hover.png"), auto;
}

.learn-code-input:active {
  color: var(--blue);
  background-color: var(--BlackBlue);
  box-shadow: 0 2px var(--blue);
  transform: translateY(4px);
  transition-duration: 0.03s;
}
.div-menu ul .li-learn:hover {
  text-decoration: underline #fff 3.5px;
}
@media only screen and (min-width: 600px) {
  .learn-code-input {
    /*TEXT*/
    font-size: 20px;
    /*Size*/
    width: 190px;
    height: 40px;
    float: right;
    padding: 0 0;
    margin: 10px -150px 0 0px;
  }
}
