/** @format */

.button-hamburgerIcon {
  width: 30px;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  margin-top: 15px;
  margin-left: 14px;
  margin-right: auto;
  background-color: transparent;
}

.button-hamburgerIcon .img-hamburger {
  height: 32px;
  vertical-align: middle;
}

.button-hamburgerIcon:active {
  transform: scale(0.9);
}

/*icon pregunta*/
@media only screen and (min-width: 600px) {
  .button-hamburgerIcon {
    margin-left: auto;
    margin-right: 14px;
  }
  .div-menu {
    margin-left: calc(-100vw + 100%);
  }
}
