/** @format */
:root {
  --inner: #e2dfb6;
  --border: black;
  --accent: white;
}
.wellcome-div {
  text-align: center;

  background-color: #262138;

  height: 640px;
}
.wellcome-div .level-div {
  padding: 0 0 20px 0px;
  height: 80px;
}
.wellcome-div .price-div {
  padding: 0 0 0px 0px;
  display: block;
  height: 80px;
  width: 100%;
}

.wellcome-div .price-div .coin {
  width: 20px;
  margin-bottom: -5px;
}
.wellcome-div h1 {
  padding: 50px 0px 60px 0px;
  font-size: 50px;
  color: #908fbb;
  font-family: "Press Start 2P";
  text-shadow: 5px -1px #66658d;
}
.wellcome-div h2 {
  color: #fff;
  margin: 30px;
  font-size: 17px;
  letter-spacing: 6px;
}
.wellcome-div p {
  padding: 40px 0px 40px 0px;
  color: #fff;
  font-family: "Press Start 2P";
  background-color: #66658d;
}
.wellcome-div .level-div .button-div {
  display: block;
  float: left;

  height: 100%;
  width: 33.33%;
}
.wellcome-div .price-div .button-div {
  display: block;
  float: left;

  height: 100%;
  width: 50%;
  padding: 0px;
}
.endScreen {
  padding: 0 0 0px 0px;
  display: block;
  height: auto;
  width: 100%;
  margin-top: 92px;
}
/*Desktop*/
@media only screen and (min-width: 1000px) {
  .wellcome-div .filterdiv {
    box-sizing: border-box;
    width: 100%;
  }
  .wellcome-div .level-div {
    width: 50%;
    display: block;
    padding: 0 0 20px 0px;
    display: block;
    float: left;
    padding: 0;
    margin: 0;
    height: 100%;
  }
  .wellcome-div .price-div {
    display: block;
    padding: 0 0 20px 0px;
    margin-top: -30px;
    width: 50%;
    display: block;
    float: left;
    padding: 0;
    margin: 0;
    height: 100%;
  }
}
